a {
    text-decoration: none;  /* 밑줄 제거 */
    color: black;           /* 기본 텍스트 색상 검정 */
  }
  
  a:visited {
    color: black;           /* 방문한 링크의 색상도 검정으로 */
  }
  
  a:hover {
    text-decoration: none;  /* 호버 시 밑줄 제거 */
    color: black;           /* 호버 시에도 검정색 */
  }
  
  a:active {
    color: black;           /* 클릭 시에도 검정색 */
  }